<template>
  <div class="hello">
    <div class="container">
      <div class="bg-white">
        <div class="text-center text-gray" style="padding: 10px 0;font-size: 12px;">
          <p><span contenteditable="true">2018/08/18</span> 借款¥<span contenteditable="true">500.00</span></p>
          <p>该笔借款前7天免利息</p>
        </div>
        <div>
          <div class="slider-wrap">
            <div style="position: relative;" class="slider-father">
              <van-slider v-model="value" :step="10" bar-height="1px" @change="onChange" />
            </div>
          </div>
        </div>
      </div>
      <div></div>
    </div>
    <div class="text-center text-gray-lt" style="margin-top: 30px;font-size: 14px;">
      <p><span class="text-blue">更多详情</span></p>
      <p>本服务由微众银行提供</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      value: 0,
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    onChange (val) {
      console.log(val)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .hello {
    padding: 10px;
  }

  .container {
    border-radius: 3px;
    border: 1px solid #ddd;

    .slider-wrap {
      padding: 30px 68px;
    }

    .slider-father {
      &:before {
        content: '';
        background-color: #e7bc2b;
        display: inline-block;
        width: 8px;
        height: 8px;
        position: absolute;
        top: 50%;
        left: -8px;
        z-index: 1;
        border-radius: 50%;
        transform: translate3d(50%,-50%,0);
      }
    }
  }
</style>



